body{
    background-color: #000;
}
header{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    display: flex;
    justify-content:space-between;
    align-items:center;
    padding:30px 100px;
    z-index: 100;
    .logo img{
        max-width:150px;
    }
    .nav{
        display:flex;
        li{
            list-style: none;
            margin:0 10px;
            a{
                color: #fff;
                text-decoration: none;
                font-weight: 500;
                letter-spacing: 1px;
            }
        }
    }
    .search{
        position: relative;
        width: 300px;
        height:40px;
        input{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            color: #fff;
            background:transparent;
            border: 1px solid rgba(255, 255, 255, 0.5);
            outline: none;
            border-radius: 4px;
            padding:0 10px 0 45px;
            backdrop-filter: blur(10px);
        }
    }
}
.nav li:hover a{
    color:#de0611;
}
.search input::placeholder{
    color: #fff;
}
.search .fa-search{
    position: absolute;
    top:50%;
    left:10px;
    transform:translateY(-50%);
    padding-right: 10px;
    color: #fff;
    border-right: 1px solid #fff;
}
// home 
.banner{
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding: 0 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .bg{
        position:absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.content{
    position: relative;
    z-index: 1;
    max-width: 550px;
    h4{
        color: rgba(255, 255, 255, 0.5);
        font-weight: 500;
        span{
            padding: 0 10px;
            border-right: 1px solid rgba(255, 255, 255, 0.5);
            i{
                background-color:#de0611;
                color: #fff;
                padding: 0 8px;
                display: inline-block;
                font-style: normal;
                border-radius: 3px;
            }
        }
        span:first-child{
            padding-left: 0;
        }
        span:last-child{
            border-right: none;
        }
    }
    p{
        font-size: 1em;
        font-weight: 300;
        line-height: 1.5em;
        color: #fff;
        margin: 10px 0 20px;
    }
    .buttons{
        position: relative;
        a{
            position: relative;
            display: inline-block;
            margin-right: 10px;
            background-color: #de0611;
            color: #fff;
            padding:6px 20px;
            text-decoration: none;
            font-weight: 500;
            letter-spacing: 1px;
            text-transform: uppercase;
        }
        a:nth-child(2){
            background-color: rgba(0, 0, 0, 0.5);
            border: 1px solid rgba(255, 255, 255, 0.1);
        }
        a:hover:nth-child(2){
            background-color:#de0611;
        }
    }

}
.movieTitle{
    max-width:250px;
}
.play{
    position:absolute;
    bottom:50px;
    left: 100px;
    display: inline-flex;
    justify-content:flex-start;
    align-items:center;
    color: #fff;
    text-transform:uppercase;
    font-weight: 500;
    text-decoration: none;
    letter-spacing: 1px;
    font-size: 1.2em;
    cursor: pointer;
    img{
        margin-right: 10px;
        max-width: 50px;
    }
}
.trailer{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content:center;
    align-items:center;
    backdrop-filter: blur(20px);
    visibility: hidden;
    video{
        max-width: 900px;
        outline: none;
    }
}
.active{
    visibility: visible;
    opacity: 1;
}
.close{
    position: absolute;
    top: 30px;
    right: 30px;
    cursor: pointer;
    filter: invert(1);
    max-width: 32px;
}

.not-found{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.6;
    img{
        width: 100vw;
        height: 100vh;
        object-fit: cover;
    }
}
// responsive 
// tablet
@media (max-width:991px) 
{
    header {
        padding:20px 50px;
        flex-direction: column;
    }
    .nav{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin: 10px 0;
    }
    .banner{
        padding:100px 50px;
        .play{
            left: 50px;
            font-size:1em;
            img{
                margin-right: 10px;
                max-width:40px;
            }
        }
    }
    .trailer video {
        max-width:90%;
    }
}
// mobile
@media (max-width:600px){
    header {
        padding:20px;
    }
    .search{
        position: relative;
        width: calc(100% -20px);
        height:40px;
    }
    .banner{
        padding: 220px 20px 120px;
        .play{
            left: 20px;
            bottom: 30px;
            font-size:1em;
        }
    }
}