@import './variables.scss';
.container{
    display: flex;
    flex-wrap: wrap;
    justify-content:center;
    position:relative;
    top: 100px;
    padding-top: 40px;
    padding-left: 40px;
    border-top: 1px solid #fff;
    h1{
        width: 100%;
        color: #fff;
    }
}
img{
    width: 100%;
}
.movie-card {
    width: 300px;
    margin: 1rem;
    padding: 1rem;
    box-shadow:5px 10px 18px #888888;
    position: relative;
    overflow: hidden;
    border-radius: 3px;
    color: #fff;
}
.movie-des{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 1rem;
    .movie-inf{
        width:80%;
    }
    .movie-rate{
        background-color:#22254b;
        height:36px;
        font-size: 16px;
        font-weight: bold;
        padding: 0.25rem 0.5rem;
        border-radius: 5px;
    }
}
.overview{
    position: absolute;
    padding:10px;
    background-color: #000;
    color: #fff;
    transform:translateY(100%);
    transition: transform 0.3s ease-in;
}

.movie-card:hover .overview{
    transform:translateY(-100%);
}
// responsive
@media (max-width: 991px){
    .container{
        margin-top:80px;
    }
}


@media (max-width: 600px){
    .container{
        margin-top:100px;
        padding-left: 24px;
    }
}